                        <li> <strong>GENERAL PROVISIONS</strong>
                            <ol>
                                <li> These rules (hereinafter - the Rules) determine the procedure and conditions of
                                    participation in the Referral Program "Invite a friend" (hereinafter - the Program).
                                </li>
                                <li>The purpose of the Program: to identify the best specialists in the IT industry,
                                    interested in cooperating with the Organizer.</li>
                                <li>Founder and organizer of the Program: ALL STARS - IT UKRAINE Limited Liability
                                    Company, located at: Ukraine, Kyiv, 01023, Sports Square, building 1-A, floor 12;
                                    USREOU code: 40581791; e-mail: jobs@allstars-it.com.</li>
                                <li>
                                    Referral (Candidate) - an individual who meets the following requirements:
                                    <ul>
                                        <li>A citizen of Ukraine or a person who has a permanent residence permit in
                                            Ukraine;</li>
                                        <li>The person has reached the age of eighteen;</li>
                                        <li>Able-bodied individual;</li>
                                        <li>has sufficient skills and experience in the field of information technology
                                            (IT) to be considered as a candidate for a position from the list of open
                                            positions of the Organizer, listed at:
                                            <a
                                                href="https://allstars-it.com/ukraine/careers/">https://allstars-it.com/ukraine/careers/</a>;
                                        </li>
                                        <li>During the 6 (six) calendar months preceding the moment of the Notification
                                            of the Referral, the specified person did not have any legal relations with
                                            the Organizer (including labor or civil law);</li>
                                        <li>During the 6 (six) calendar months preceding the moment of the Notification
                                            of the Referral, the specified person was not in the database of the
                                            Organizer of potential employees or contractors;</li>
                                        <li>Has the desire and ability to cooperate with the Organizer on the basis of a
                                            civil contract on a long-term basis.</li>
                                    </ul>
                                </li>
                                <li>
                                    Referee (Participant) - a natural person who meets the following requirements:
                                    <ul>
                                        <li>a citizen of Ukraine or a person who has a permanent residence permit in
                                            Ukraine;</li>
                                        <li>has reached the age of eighteen;</li>
                                        <li>able-bodied individual;</li>
                                        <li>in the manner prescribed by the Rules, submitted a Notice of Referral to the
                                            Organizer.</li>
                                    </ul>
                                </li>
                                <li>Informing about the Program is done by posting these Rules, as well as publishing
                                    relevant information on the Organizer's website.</li>
                                <li>Acquaintance with the terms of these Rules is carried out by the Referee and the
