                        </li>
                        <li> <strong>PROCEDURE FOR PARTICIPATION IN THE PROGRAM</strong>

                            <ol>
                                <li> A person who wishes to participate in the Program as a Referee and meets all the
                                    criteria set forth in the Rules shall submit a Referral Notice (which must meet all
                                    the criteria set forth in the Rules) by filling out the prescribed form (hereinafter
                                    - the Notice) posted on the Website. Organizer.</li>
                                <li>The message, in particular, should contain the following information:
                                    <ul>
                                        <li>name, surname and patronymic of the Referee;</li>
                                        <li>e-mail address, telephone number of the Referee;</li>
                                        <li>name, surname and patronymic of the Referral;</li>
                                        <li>e-mail address, telephone number of the Referral;</li>
                                        <li>Referral's permanent residence;</li>
                                        <li>position from the list of open positions of the Organizer, indicated at the
                                            address: <a
                                                href="https://allstars-it.com/ukraine/careers/">https://allstars-it.com/ukraine/careers/</a>,
                                            to which the Referral is
                                            offered;</li>
                                        <li>information on education, experience and work skills (in the form of a
                                            summary) of the Referral.</li>
                                    </ul>

                                    All other notices that will be sent to the Organizer in any other form, as well as
                                    incorrectly filled in notices are not considered submitted and are not accepted for
                                    consideration.
                                    To complete the Message, you must have a profile in the Gmail mail service.
                                </li>
                                <li>The Referral does not have the right to submit a Notice regarding himself as a
                                    Referral.</li>
                                <li>By submitting the Notice, the Referee:
                                    <ul>
                                        <li>agrees and accepts the terms of the Rules, guarantees their unconditional
                                            observance;</li>
                                        <li>guarantees the accuracy of all information and information specified by him
                                            in the Communication, and is aware of the negative consequences that may
                                            have the provision of inaccurate information / information;</li>
                                        <li>agrees to the processing and use of his personal data by the Organizer
                                            within the Program;</li>
                                        <li>guarantees and can provide evidence that he / she has duly and fully
                                            acquainted the Referral in respect of which he / she is notified with the
                                            terms of the Program, in particular with these Rules, and obtained his / her
                                            consent to cooperate with the Organizer;</li>
                                        <li>guarantees and can provide evidence that it has duly informed the Referral
                                            in respect of which it is notified, obtained its unconditional consent to
                                            participate in the Program as a Referral, including the submission of the
                                            Notice of his candidacy, submission of his resume, and any additional
                                            information within the Program;</li>
                                        <li>guarantees and can provide evidence that he / she has duly obtained the
                                            unquestioning consent of the Referral for the processing and use of his /
