                        <li> <strong>REFERRAL CONTRACT AGREEMENT AND REWARD PAYMENT</strong>
                            <ol>
                                <li>The Notification of the Referee participates in the Program within two calendar
                                    months from the moment of sending by the Organizer of the e-mail to the Referee with
                                    confirmation that the Notification is submitted in accordance with the requirements
                                    of the Rules.</li>
                                <li>The Organizer, solely at its own discretion, based on its own criteria and needs,
                                    decides on concluding a contract with the Referral for the provision of services, in
                                    which it will act as a customer of services in the field of information technology
                                    and / or software development.</li>
                                <li>The Referee has the right to receive the Remuneration in accordance with the terms
                                    of the Procedure, if within two calendar months from the date of sending the
                                    Organizer an e-mail to the Referee confirming that the Notice is submitted by him in
                                    accordance with the Rules, the Organizer</li>
                                <li> The amount of remuneration that the Organizer undertakes to pay to the Referee:
                                    <ol>
                                        <li>Remuneration in the amount of UAH 27,846.10 (which at the time of
                                            publication of the Rules is equal to USD 1,000.00 at the official exchange
                                            rate of the National Bank of Ukraine) has the right to receive the Referee,
                                            if in accordance with its Submission the Customer has concluded a contract
                                            with the Referral Middle / Senior;</li>
                                        <li>Remuneration in the amount of UAH 55,692.20 (which at the time of
                                            publication of the Rules is equal to USD 2,000.00 at the official exchange
                                            rate of the National Bank of Ukraine) has the right to receive the Referee,
                                            if in accordance with its Submission the Customer has concluded a contract
                                            with the Referral Team Lead, Tech Lead, Senior Devops Software Artichects.
                                        </li>
                                    </ol>
                                </li>
                                <li>In case of decrease or increase of the official exchange rate of hryvnia to the US
                                    dollar set by the National Bank of Ukraine on the date of payment of the monetary
                                    incentive by the Organizer compared to the official exchange rate of hryvnia to the
                                    US dollar set by the National Bank of Ukraine on the date of publication of the
                                    Rules, the Organizer shall make appropriate monetary incentives. paragraph 3.4 of
                                    the Rules, in the amount determined by the formula: S = X / Y x Z, where <br>
                                    S - the amount of remuneration due to the Referee in accordance with paragraph 3.4
                                    of the Rules, taking into account the recalculation provided for in paragraph 3.5 of
                                    the Rules related to the change in the official exchange rate of hryvnia to the US
                                    dollar;<br>
                                    X - the corresponding amount of remuneration provided for in paragraph 3.4 of the
                                    Rules;<br>
                                    Y - the official exchange rate of hryvnia to the US dollar, set by the National Bank
                                    of Ukraine on the date of publication of the Rules;<br>
                                    Z is the official exchange rate of hryvnia to the US dollar set by the National Bank
                                    of Ukraine on the date of payment of the remuneration by the Organizer.
                                </li>
                                <li>The organizer informs that in accordance with the requirements of the Tax Code of
                                    Ukraine, the income of an individual is included in his income, which is subject to
