                                    The norm provided for in part two of this paragraph shall not apply to cases where
                                    the Referee is a natural person - an entrepreneur.</li>
                                <li>Within ten working days from the moment of concluding an agreement on the provision
                                    of services between the Organizer and the Referral, the Organizer, in accordance
