<script>
    import $ from 'jquery';
    export default {
        data() {
            return {
                activeTab: 'en'
            }
        },
        methods: {
            tabSwitch(e) {
                let tar = $(e.target).children().length ? e.target : $(e.target).parent();
                let id = $(tar).attr('href');
                this.activeTab = id.replace('#', '');
                $('.tab').hide();
                $(id).show();
            }
        },
        mounted() {
            $('head').append(`
            <style>
              body::after {

    background: radial-gradient(rgba(255, 255, 255, 0.9), rgba(250, 250, 250, 1));
    opacity: 0.6;
  }
            </style>
            `)
        }
    }
</script>

<style scoped>
    #ua {
        display: none;
    }

    .terms {
        min-height: 100vh;

    }

    .terns-inner {
        margin-top: 15vh;
    }

    ol {
        list-style-type: none;
        counter-reset: item;
        margin: 0;
        padding: 0;
    }

    ul {
        list-style-type: initial;
    }

    ol>li {
        display: table;
        counter-increment: item;
        margin-bottom: 0.6em;
    }

    ol>li:before {
        content: counters(item, ".") ". ";
        display: table-cell;
        padding-right: 0.6em;
    }

    ol>li li {
        margin: 0;
    }

    ol>li>ol>li:before {
        content: counters(item, ".") " ";
    }

    li {
